.post {
  background-color: rgb(89, 0, 179);
  max-width: 50%;
  margin: 15px auto;
  border: 1px solid rgba(211, 211, 211, 0);
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.infoPanel {
  display: flex;
  max-width: 95%;
  margin: auto;
  margin-top: 10px;
  border-radius: 10px;
  background-color: rgb(77, 0, 153);
  color: white;
  font-family: Helvetica;
}

.contentPanel {
  padding-top: 12px;
  max-width: 95%;
  margin: auto;
  color: white;
  font-family: Helvetica;
}

.userInfo {
  display: flex;
  flex-grow: 15;
  flex-direction: column;
  justify-content: center;
}

.datePanel {
  flex-grow: 1;
  padding-top: 0.5rem;
  font-size: 100%;
}

.namePanel {
  padding-left: 1rem;
  font-size: 190%;
  padding-bottom: 5px;
}

.contentImg {
  display: flex;
  justify-content: center;
}

.postImg {
  border-radius: 10px;
  width: 100%;
  margin-bottom: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.likePanel {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
}

.contentDiscription {
  padding-bottom: 10px;
  font-size: 125%;
}

@media only screen and (max-width: 600px) {
  .post {
    max-width: 95%;
  }

  .avatarImg {
    width: 50px;
    height: 50px;
  }

  .avatar {
    padding: 3px 10px;
  }

  .datePanel {
    font-size: 70%;
  }

  .namePanel {
    font-size: 100%;
  }

  .nickNamePanel {
    font-size: 70%;
  }

  .contentDiscription {
    font-size: 90%;
  }
}
